/*
The main SCSS file for everything, yep
*/

@mixin shadow($level: 1) {
  @if $level == 1 {box-shadow: 0 1px 3px rgba(20, 39, 53,0.12), 0 1px 2px rgba(20, 39, 53,0.24);}
  @else if $level == 2 {box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);}
  @else if $level == 3 {box-shadow: 0 10px 20px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23);}
  @else if $level == 4 {box-shadow: 0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22);}
  @else if $level == 5 {box-shadow: 0 19px 38px rgba(0,0,0,0.30), 0 15px 12px rgba(0,0,0,0.22);}
}

@import "main";
@import "layout";
#index-grid {
  padding: 0 1rem;
}
#index-grid .column {
  padding: 0 2rem;
  margin: 0 0 3rem 0;
}
.rounded {
  border-radius: 4px;
  margin: 0 0 0.5rem 0;
  @include shadow(1);
  transition: all 250ms ease-in-out;
  &:hover {
    transform: scale(1.02);
    cursor: pointer;
  }
}
.generator-image {
  position: relative;
  z-index:1;
}

.generator-image--overlay {
  position: absolute;
  top:0;
  bottom: 0;
  left:0;
  right:0;
  background: rgba(000, 000, 000, 0.8);
  height: 100%;
  width: 100%;
  z-index: 2;
}

.generator-image--content {
  position: absolute;
  width: 100%;
  top: 46%;
  text-align: center;
  z-index: 3;
  color: rgba(255, 255, 255, 0.9);
}

.generator-image--content > a {
  font-size:17px;
  color: rgba(155, 232, 33, 0.9);
}



.generator-content {
  margin: 4rem 0 0 0;
}

#overlay, #function2, #function3, #function4 {
  display: none;
}

.post-title {
  font-size:3rem;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-weight: bold;
}

#action1 {
  margin: 2rem 0 1rem 0;
}
#function1 {
  color: aliceblue !important;
  color: #8993A0;
}
#function1 > p {
  margin: 0 0 0.5rem 0;
}

#action2, #action4 { margin: 0 0 1rem 1rem;}
#activation {
  display:none;
}
.green {
  color: #9BE821;
}

.red {
  color: red;
}

.contrast {
  color: aliceblue;
}

.generator-status {
  margin: 0 0 6px 0;
}

.site-header {
  padding: 1rem 0;
  margin: 0 0 0.5rem 0;
  text-align:center;
}
.site-title {
  font-size:34px;
}

.site-footer {
  padding: 1rem 0;
  margin: 6rem 0 0 0;
  color: aliceblue;
}

.footer-nav a:not(:last-child) {
  margin: 0 1rem 0 0;
}

.generator-aside {
  font-size:14px;
}
